<template>

<section id="Products" style="min-height: 100vh; width: 100%; justify-content: center; flex-direction: column; display: flex; ">

  <div class="container d-flex justify-content-end mb-2 mt-5 pt-3">
    <div class="d-flex w-25 ms-3">
      <label for="" class="form-label">Sort by category
      </label>

      <select
        class="form-select"
        name=""
        id="sortCategory"
        onchange="sortCategory()">
        <option value="All">All</option>
        <option value="RnB">RnB</option>
        <option value="Soul RnB">Soul RnB</option>
      </select>
    </div>
  </div>
  <div id="products" class="container d-flex mb-3">
 </div>
</section>
</template>

<script>
export default {
  components: {},
  mounted() {
    let products = JSON.parse(localStorage.getItem("products"))
      ? JSON.parse(localStorage.getItem("products"))
      : [
          {
            title: "Cardi B - Privacy Album",
            category: "RnB",
            price: 249.99,
            img: "https://m.media-amazon.com/images/I/91OQaNrPq3L._SL1500_.jpg",
          },

          {
            title: "Cordae - From Birds Eye View Album",
            category: "RnB",
            price: 229.99,
            img: "https://images.complex.com/complex/images/c_fill,dpr_auto,f_auto,q_auto,w_1400/fl_lossy,pg_1/msepv1rnhtxqvfu5kluw/cordae?fimg-ssr",
          },

          {
            title: "Drake - Certified Lover Boy Album",
            category: "RnB",
            price: 279.99,
            img: "https://media.pitchfork.com/photos/613214a114458bf5df99f2a9/1:1/w_320,c_limit/Drake-Certified-Lover-Boy.png",
          },

          {
            title: "Chris Brown - Indigo Album",
            category: "RnB",
            price: 319.99,
            img: "https://imgs.capitalxtra.com/images/51868?crop=16_9&width=660&relax=1&signature=HflF02TSIRid2KhhiLbmwyUosCg=",
          },
          {
            title: "Giveon - When It's All Said And Done Album",
            category: "Soul RnB",
            price: 269.99,
            img: "https://okayplayer-wpengine.netdna-ssl.com/wp-content/uploads/2020/03/giveons-debut-ep-take-time-is-a-vintage-love-letter-768x768.jpg",
          },
          {
            title: "Young Thug - Punk Album",
            category: "RnB",
            price: 399.99,
            img: "https://media.pitchfork.com/photos/616e3df2a21726d2eb3df5fa/1:1/w_320,c_limit/100000x100000-999.jpeg",
          },

          {
            title: "Travis Scott - AstroWorld Album",
            category: "RnB",
            price: 249.99,
            img: "https://media.pitchfork.com/photos/5b60c32dc50e6c2e339b99fe/1:1/w_320,c_limit/Travis%20Scott_Astroworld.jpg",
          },
          {
            title: "Daniel Caesar - Freudian Album",
            category: "Soul RnB",
            price: 269.99,
            img: "https://i.cbc.ca/1.4574008.1520952868!/fileImage/httpImage/image.jpg_gen/derivatives/16x9_780/daniel-caesar-freudian.jpg",
          },
          {
            title: "Doja Cat - Planet Her Album",
            category: "RnB",
            price: 399.99,
            img: "https://upload.wikimedia.org/wikipedia/pt/1/16/Planet_Her_-_Doja_Cat.png",
          },

          {
            title: "Don Toliver - Life Of A DON Album",
            category: "RnB",
            price: 269.99,
            img: "https://media.pitchfork.com/photos/6163df5c436f0bb0842845be/1:1/w_320,c_limit/100000x100000-999.jpeg",
          },
          {
            title: "Masego - Studying Abroad: Extended Stay Album",
            category: "Soul RnB",
            price: 269.99,
            img: "https://media.pitchfork.com/photos/5b9685e6ec087f2ca7e0ae8b/1:1/w_320,c_limit/lady%20lady.jpg",
          },
          {
            title: "Joji - Nectar Album",
            category: "Soul RnB",
            price: 269.99,
            img: "https://images.complex.com/complex/images/c_fill,dpr_auto,f_auto,q_auto,w_768/fl_lossy,pg_1/m7ll2zgzoxostwcoswzi/joji-nectar?fimg-ssr",
          },
        ];

    let cart = JSON.parse(localStorage.getItem("cart"))
      ? JSON.parse(localStorage.getItem("cart"))
      : [];

    // READ
    function readProducts(products) {
      document.querySelector("#products").innerHTML = "";
      products.forEach((product, position) => {
        document.querySelector("#products").innerHTML += `
      <div class="card">
        <img src="${product.img}" class="card-img-top" alt="${product.title}">
        <div class="card-body">
          <h5 class="card-title">${product.title}</h5>
          <p class="card-text">R${product.price}</p>
          <div class="d-flex mb-3">
            <input type="number" class="form-control" value=1 min=1 id="addToCart${position}">
            <button type="button" class="btn btn-secondary ms-3" onclick="addToCart(${position})"><i class="fas fa-cart-plus"></i></button>
          </div>
        </div>

          <div class="d-flex justify-content-end card-footer">
            <button type="button" class="btn btn-primary w-50" data-bs-toggle="modal" data-bs-target="#editProduct${position}" >
              Edit
            </button>

            <button type="button" class="btn btn-danger w-50 ms-3" onclick="deleteProduct(${position})" >
              <i class="fa-solid fa-trash"></i>
            </button>
          </div>
      </div>

      <div
        class="modal fade"
        id="editProduct${position}"
        tabindex="-1"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true">

                <div class="modal-dialog">
                  <div class="modal-content">
                    <div class="modal-header">
                      <h5 class="modal-title" id="exampleModalLabel">
                        Edit ${product.title}
                      </h5>
                      <button
                        type="button"
                        class="btn-close"
                        data-bs-dismiss="modal"
                        aria-label="Close"></button>
                    </div>
                    <div class="modal-body">
                      <div class="mb-3">
                        <label for="editTitle${position}" class="form-label">Title</label>
                        <input
                          class="form-control"
                          type="text"
                          name="editTitle${position}"
                          id="editTitle${position}"
                          value="${product.title}"/>
                      </div>
                      <div class="mb-3">
                        <label for="editCategory${position}" class="form-label">Category</label>
                        <select
                          class="form-select"
                          name="editCategory${position}"
                          id="editCategory${position}">
                          <option value="All">All</option>
                      <option value="RnB">RnB</option>
                      <option value="Dubstep">Dubstep</option>
                      <option value="Soul RnB">Soul RnB</option>
                      <option value="PSY">PSY</option>
                        </select>
                      </div>
                      <div class="mb-3">
                        <label for="editPrice${position}" class="form-label">Price</label>
                        <input
                          class="form-control"
                          type="text"
                          name="editPrice${position}"
                          id="editPrice${position}"
                          value="${product.price}"/>
                      </div>
                      <div class="mb-3">
                        <label for="editImg${position}" class="form-label">Image URL</label>
                        <input
                          class="form-control"
                          type="text"
                          name="editImg${position}"
                          id="editImg${position}"
                          value="${product.img}"/>
                      </div>
                    </div>
                    <div class="modal-footer">
                      <button
                        type="button"
                        class="btn btn-secondary"
                        data-bs-dismiss="modal">
                        Close
                      </button>
                      <button
                        type="button"
                        class="btn btn-primary"
                        data-bs-dismiss="modal"
                        onclick="updateProduct(${position})">
                        Save changes
                      </button>
                    </div>
                  </div>
                </div>
              </div>`;
      });
    }

    readProducts(products);
    showCartBadge();

    // CREATE
    function createProduct() {
      let title = document.querySelector("#addTitle").value;
      let category = document.querySelector("#addCategory").value;
      let price = document.querySelector("#addPrice").value;
      let img = document.querySelector("#addImg").value;

      try {
        if (!title || !price || !img)
          throw new Error("Please fill in all fields");
        products.push({
          title,
          category,
          price,
          img,
        });

        localStorage.setItem("products", JSON.stringify(products));
        readProducts(products);
      } catch (err) {
        alert(err);
      }
    }

    // UPDATE
    function updateProduct(position) {
      let title = document.querySelector(`#editTitle${position}`).value;
      let category = document.querySelector(`#editCategory${position}`).value;
      let price = document.querySelector(`#editPrice${position}`).value;
      let img = document.querySelector(`#editImg${position}`).value;

      try {
        if (!title || !price || !img)
          throw new Error("Please fill in all fields");
        products[position] = {
          title,
          category,
          price,
          img,
        };
        localStorage.setItem("products", JSON.stringify(products));
        readProducts(products);
      } catch (err) {
        alert(err);
      }
    }

    // DELETE
    function deleteProduct(position) {
      let confirmation = confirm(
        "Are you sure you want to delete the selected product?"
      );

      if (confirmation) {
        products.splice(position, 1);
        localStorage.setItem("products", JSON.stringify(products));
        readProducts(products);
      }
    }

    // ADD TO CART
    function addToCart(position) {
      let qty = document.querySelector(`#addToCart${position}`).value;
      let added = false;
      cart.forEach((product) => {
        if (product.title == products[position].title) {
          alert(
            `You have successfully added ${qty} ${products[position].title} to the cart`
          );
          product.qty = parseInt(product.qty) + parseInt(qty);
          added = true;
        }
      });
      if (!added) {
        cart.push({ ...products[position], qty });
        alert(
          `You have successfully added ${qty} ${products[position].title} to the cart`
        );
      }

      showCartBadge();

      localStorage.setItem("cart", JSON.stringify(cart));
    }

    // Update Cart Badge
    function showCartBadge() {
      document.querySelector("#badge").innerHTML = cart ? cart.length : "";
    }

    // SORT BY CATEGORY
    function sortCategory() {
      let category = document.querySelector("#sortCategory").value;

      if (category == "All") {
        return readProducts(products);
      }

      let foundProducts = products.filter((product) => {
        return product.category == category;
      });

      readProducts(foundProducts);
      console.log(foundProducts);
    }

    // SORT BY NAME
    function sortName() {
      let direction = document.querySelector("#sortName").value;

      let sortedProducts = products.sort((a, b) => {
        if (a.title.toLowerCase() < b.title.toLowerCase()) {
          return -1;
        }
        if (a.title.toLowerCase() > b.title.toLowerCase()) {
          return 1;
        }
        return 0;
      });
      if (direction == "descending") sortedProducts.reverse();
      console.log(sortedProducts);
      readProducts(products);
    }

    // SORT BY PRICE
    function sortPrice() {
      let direction = document.querySelector("#sortPrice").value;

      let sortedProducts = products.sort((a, b) => a.price - b.price);

      console.log(sortedProducts);

      if (direction == "descending") sortedProducts.reverse();
      readProducts(sortedProducts);
    }
  },
};
</script>

<style>
#products {
  flex-wrap: wrap;
  gap: 20px;
}

img {
  height: 200px !important;
  object-fit: cover;
}

.card {
  width: calc((100% / 4) - 15px);
  min-width: 300px;
  margin-bottom: 150px;
}

.card-title {
  text-transform: capitalize;
}

#Products {
    background:linear-gradient(0deg, rgba(42, 87, 13, 0.062), rgba(59, 83, 20, 0.315)), url('../images/www.png'); 
    background-repeat: no-repeat;
    background-attachment: fixed; 
    backface-visibility:visible;
    background-size: cover;
  }
</style>
